//
// Component: Small Box
//

.small-box {
  border-radius: .5rem;
  display: block;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;

  // content wrapper
  > .inner {
    padding: 10px;
    min-height: 200px;
  }

  > .small-box-footer {
    background: rgba($black, 0.1);
    color: rgba($white, 0.8);
    display: block;
    padding: 10px 0;
    position: relative;
    text-align: center;
    text-decoration: none;
    z-index: 10;

    &:hover {
      background: rgba($black, 0.15);
      color: $white;
    }
  }

  h3 {
    @include font-size(2.2rem);
    font-weight: bold;
    margin: 0 0 10px 0;
    padding: 0;
    white-space: nowrap;
  }

  @include media-breakpoint-up(lg) {
    .col-xl-2 &,
    .col-lg-2 &,
    .col-md-2 & {
      h3 {
        @include font-size(1.6rem);
      }
    }

    .col-xl-3 &,
    .col-lg-3 &,
    .col-md-3 & {
      h3 {
        @include font-size(1.6rem);
      }
    }
  }

  @include media-breakpoint-up(xl) {
    .col-xl-2 &,
    .col-lg-2 &,
    .col-md-2 & {
      h3 {
        @include font-size(2.2rem);
      }
    }

    .col-xl-3 &,
    .col-lg-3 &,
    .col-md-3 & {
      h3 {
        @include font-size(2.2rem);
      }
    }
  }

  p {
    font-size: 1rem;

    > small {
      color: $gray-100;
      display: block;
      font-size: 0.9rem;
      margin-top: 5px;
    }
  }

  h3,
  p {
    z-index: 5;
  }

  // the icon
  .icon {
    color: rgba($black, 0.15);
    z-index: 0;

    > i {
      font-size: 90px;
      position: absolute;
      right: -10px;
      bottom: 60px;
      transition: all .3s linear;
    }

  }

  // Small box hover state
  &:hover {
    text-decoration: none;

    // Animate icons on small box hover
    .icon > i {
      font-size: 130px;
      opacity: 0.5;
    }
  }
}

@include media-breakpoint-down(sm) {
  // No need for icons on very small devices
  .small-box {
    text-align: center;

    .icon {
      display: none;
    }

    p {
      font-size: 12px;
    }
  }
}
